.App {
  position: relative;
}

.op-bordered {
  border-radius: 25px;
  border: 2px solid #73AD21;
  background-color: midnightblue;
}

.compare-inner {
  grid-area: inner-div;
}

.compare-container {
  margin: 0 auto;
  display: grid;
  place-items: center;
  grid-template-areas:
                  "inner-div";
}